import styled from "styled-components";
import { useForm } from "react-hook-form";
import {
  AnimatePresence,
  motion,
  useAnimation,
  useViewportScroll,
} from "framer-motion";
import { Link, useRouteMatch, useHistory } from "react-router-dom";
import { route } from "../Route";
import { useEffect, useState } from "react";

const Nav = styled(motion.nav)`
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  width: 100%;
  top: 0;
  font-size: 1rem;
  padding: 1.5rem 4rem;
  color: white;
  background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0));
`;

const Column = styled.div`
  display: flex;
  align-items: center;
`;

const Logo = styled(motion.svg)`
  margin-right: 3rem;
  width: 5.7rem;
  height: 1.6rem;
  fill: ${(props) => props.theme.red};
  path {
    stroke-width: 0.5rem;
    stroke: white;
  }
`;

const Items = styled.ul`
  display: flex;
  align-items: center;
`;

const Item = styled.li`
  margin-right: 20px;
  color: ${(props) => props.theme.white.darker};
  transition: color 0.3s ease-in-out;
  position: relative;
  &:hover {
    color: ${(props) => props.theme.white.lighter};
  }
  display: flex;
  flex-direction: column;
  justify-content: center;
`;

const Search = styled.form`
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  svg {
    height: 25px;
  }
`;

const Circle = styled(motion.span)`
  position: absolute;
  width: 0.4rem;
  height: 0.4rem;
  border-radius: 50%;
  bottom: -0.8rem;
  left: 0;
  right: 0;
  margin: 0 auto;
  background-color: ${(props) => props.theme.red};
`;

const Input = styled(motion.input)`
  transform-origin: right center;
  width: 16.5rem;
  position: absolute;
  right: 0;
  padding: 0.7rem 1rem;
  padding-left: 3rem;
  z-index: -1;
  color: white;
  background-color: transparent;
  border: 1px solid ${(props) => props.theme.white.lighter};
  outline: none;
  background-color: black;
`;

const logoVariants = {
  normal: {
    fill: "rgba(255, 255, 255, 0)",
  },
  active: {
    fill: "rgb(229, 16, 19)",
    pathLength: [0, 1, 0],
  },
};

const navVariants = {
  top: {
    backgroundColor: "rgba(0,0,0,0)",
  },
  scroll: {
    backgroundColor: "rgba(0,0,0,1)",
  },
};

interface IForm {
  keyword: string;
}

export const Header = () => {
  const [searchOpen, setSearchOpen] = useState(false);
  const homeMatch = useRouteMatch(route.home);
  const tvMatch = useRouteMatch(route.tv);
  const inputAnimation = useAnimation();
  const navAnimation = useAnimation();
  const { scrollY } = useViewportScroll();
  const { register, setValue, formState, handleSubmit, setFocus } =
    useForm<IForm>();
  const toggleSearch = () => {
    if (searchOpen) {
      inputAnimation.start({
        scaleX: 0,
      });
    } else {
      inputAnimation.start({
        scaleX: 1,
      });
    }
    setSearchOpen((prev) => !prev);
    setFocus("keyword");
  };

  useEffect(() => {
    scrollY.onChange(() => {
      if (scrollY.get() > 80) {
        navAnimation.start("scroll");
      } else {
        navAnimation.start("top");
      }
    });
  }, [scrollY, navAnimation]);

  const history = useHistory();

  const onValid = (data: IForm) => {
    const { keyword } = data;
    history.push(`/search?keyword=${keyword}`);
    setValue("keyword", "");
  };

  return (
    <Nav variants={navVariants} animate={navAnimation} initial="top">
      <Column>
        <Link to={route.home}>
          <Logo
            xmlns="http://www.w3.org/2000/svg"
            width="1024"
            height="276.742"
            viewBox="0 0 1024 276.742"
          >
            <motion.path
              variants={logoVariants}
              initial="normal"
              animate="active"
              transition={{
                default: { duration: 3 },
                fill: { duration: 1, delay: 0.5 },
              }}
              d="M140.803 258.904c-15.404 2.705-31.079 3.516-47.294 5.676l-49.458-144.856v151.073c-15.404 1.621-29.457 3.783-44.051 5.945v-276.742h41.08l56.212 157.021v-157.021h43.511v258.904zm85.131-157.558c16.757 0 42.431-.811 57.835-.811v43.24c-19.189 0-41.619 0-57.835.811v64.322c25.405-1.621 50.809-3.785 76.482-4.596v41.617l-119.724 9.461v-255.39h119.724v43.241h-76.482v58.105zm237.284-58.104h-44.862v198.908c-14.594 0-29.188 0-43.239.539v-199.447h-44.862v-43.242h132.965l-.002 43.242zm70.266 55.132h59.187v43.24h-59.187v98.104h-42.433v-239.718h120.808v43.241h-78.375v55.133zm148.641 103.507c24.594.539 49.456 2.434 73.51 3.783v42.701c-38.646-2.434-77.293-4.863-116.75-5.676v-242.689h43.24v201.881zm109.994 49.457c13.783.812 28.377 1.623 42.43 3.242v-254.58h-42.43v251.338zm231.881-251.338l-54.863 131.615 54.863 145.127c-16.217-2.162-32.432-5.135-48.648-7.838l-31.078-79.994-31.617 73.51c-15.678-2.705-30.812-3.516-46.484-5.678l55.672-126.75-50.269-129.992h46.482l28.377 72.699 30.27-72.699h47.295z"
            />
          </Logo>
        </Link>
        <Items>
          <Item>
            <AnimatePresence>
              <Link to={route.home}>
                Home {homeMatch?.isExact && <Circle layoutId="circle" />}
              </Link>
            </AnimatePresence>
          </Item>
          <Item>
            <AnimatePresence>
              <Link to={route.tv}>
                TV Shows {tvMatch && <Circle layoutId="circle" />}
              </Link>
            </AnimatePresence>
          </Item>
        </Items>
      </Column>
      <Column>
        <Search onSubmit={handleSubmit(onValid)}>
          <motion.svg
            onClick={toggleSearch}
            animate={{ x: searchOpen ? -225 : 0 }}
            transition={{ type: "linear" }}
            fill="currentColor"
            viewBox="0 0 20 20"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              fillRule="evenodd"
              d="M8 4a4 4 0 100 8 4 4 0 000-8zM2 8a6 6 0 1110.89 3.476l4.817 4.817a1 1 0 01-1.414 1.414l-4.816-4.816A6 6 0 012 8z"
              clipRule="evenodd"
            ></path>
          </motion.svg>
          <Input
            {...register("keyword", {
              required: "검색어가 필요합니다",
              minLength: { value: 2, message: "두 글자 이상 입력하세요" },
            })}
            animate={inputAnimation}
            initial={{ scaleX: 0 }}
            transition={{ type: "linear" }}
            placeholder="영화 또는 TV 프로그램을 검색하세요"
          />
        </Search>
      </Column>
    </Nav>
  );
};
